const list_element = document.querySelector('.gallery');

let new_elements = '';
images.forEach(element => {
  new_elements += `
    <li class="gallery-item">
        <a class="gallery-link" href="${element.original}">
            <img
                class="gallery-image"
                src="${element.preview}"
                alt="${element.description}"
            />
        </a>
    </li>`;
});
list_element.insertAdjacentHTML('afterBegin', new_elements);

var lightbox = new SimpleLightbox('.gallery li a', {
  className: 'gallery-overley-background',
  captionDelay: 250,
  captionType: Text,
  captionsData: 'alt',
});
